<template>
  <div id="knowyourplates">
    <div class="text-center p-3">
      <div class="row">
        <div class="col-12 pb-5">
          <h1>Coming soon!</h1>
        </div>

        <div class="col-sm-12 col-md-10 col-lg-8 m-auto text-center">
          <div class="row m-3">
            <div class="col-3 text-center">
              <div class="rounded-box bg-dark p-3">
                <h1 class="display-3">{{ getDays }}</h1>
                <p>Days</p>
              </div>
            </div>

            <div class="col-3 text-center">
              <div class="rounded-box bg-dark p-3">
                <h1 class="display-3">{{ getHours }}</h1>
                <p>Hours</p>
              </div>
            </div>

            <div class="col-3 text-center">
              <div class="rounded-box bg-dark p-3">
                <h1 class="display-3">{{ getMinutes }}</h1>
                <p>Minutes</p>
              </div>
            </div>

            <div class="col-3 text-center">
              <div class="rounded-box bg-dark p-3">
                <h1 class="display-3">{{ getSeconds }}</h1>
                <p>Seconds</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "knowyourplates",

  data() {
    return {
      months: 0,
      days: 0,
      hours: 0,
      minutes: 0,
      seconds: 0
    };
  },

  mounted() {
    const vueObject = this;
    const countDownDate = new Date(2020, 6, 20).getTime();
    setInterval(function() {
      let now = new Date().getTime();
      let distance = countDownDate - now;
      vueObject.days = Math.floor(distance / (1000 * 60 * 60 * 24));
      vueObject.hours = Math.floor(
        (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      vueObject.minutes = Math.floor(
        (distance % (1000 * 60 * 60)) / (1000 * 60)
      );
      vueObject.seconds = Math.floor((distance % (1000 * 60)) / 1000);
    });
  },

  computed: {
    getDays() {
      return this.days < 10 ? "0" + this.days : this.days;
    },

    getHours() {
      return this.hours < 10 ? "0" + this.hours : this.hours;
    },

    getMinutes() {
      return this.minutes < 10 ? "0" + this.minutes : this.minutes;
    },

    getSeconds() {
      return this.seconds < 10 ? "0" + this.seconds : this.seconds;
    }
  }
};
</script>

