import Home from './components/Home';
import About from './components/about/About';
import Timeline from './components/timeline/Timeline';
import Knowyourplates from './components/projects/Knowyourplates';
import Others from './components/projects/Others';
import Contact from './components/Contact'

export const routes = [{
    path: '/',
    component: Home,
    name: 'home'
}, {
    path: '/about-me',
    component: About,
    name: 'about'
}, {
    path: '/my-timeline',
    component: Timeline,
    name: 'timeline'
}, {
    path: '/projects/knowyourplates',
    component: Knowyourplates,
    name: 'knowyourplates'
}, {
    path: '/projects/others',
    component: Others,
    name: 'otherProjects'
}, {
    path: '/contact',
    component: Contact,
    name: 'contact'
}, {
    path: '*',
    redirect: { name: 'home' }
}]